import { Injectable } from "@angular/core";
import { Subject, Observable } from "rxjs/Rx";
import { IEvent } from "app/models/event.model";
import { Http, Response } from "@angular/http";

@Injectable()

export class EventService {

    private hostUrl = 'http://laravel-rest52.dev';  // URL to web API
    private basePath = '/api/v1';  // URL to web API
    constructor(private http:Http) {
    }

    public getEvents(): Observable<IEvent[]> {
        return this.http.get(`${this.hostUrl}${this.basePath}/event`)
            .map((response: Response) => {
                const body = response.json();

                return <IEvent[]>body['events'] || {};
            })
            .catch(this.handleError);
    }

    public getEvent(id: number): Observable<IEvent> {
        return this.http.get(`${this.hostUrl}${this.basePath}/event/${id}`).map((response: Response) =>
        {
            const body = response.json();
            return <IEvent>body.event;
        }).catch(this.handleError);
    }

    public saveEvent(event) {
       // TODO
    }

    private handleError(error: Response | any) {
        console.log(error);
        
        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        console.error(errMsg);
        return Observable.throw(errMsg);
    }
}



