import { BrowserModule } from '@angular/platform-browser';
import { NgModule, APP_INITIALIZER } from '@angular/core';
import { RouterModule } from "@angular/router";
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { AlertModule } from 'ng2-bootstrap';
import { AppConfig, firebaseConfig } from './app.config';
import { initializeApp, database } from 'firebase';
import { AngularFireModule } from 'angularfire2';
import { MaterialModule } from '@angular/material';
import 'hammerjs';

import { AppComponent } from './app.component';
import { EventListComponent } from './event-list/event-list.component';
import { EventCardComponent } from './event-card/event-card.component';
import { NavbarComponent } from './navbar/navbar.component';
import { EventDetailsComponent } from './event-details/event-details.component';
import { appRoutes } from "./app.routes";
import { EventService } from "./services/event.service";
import { CreateEventComponent } from './create-event/create-event.component';
import { Error404Component } from './error-404/error-404.component';
import { EventRouteActivator } from "app/event-details/event-route-activator.service";
import { EventListResolver } from "app/event-list/event-list-resolver.component";
import { EventResolver } from "app/event-details/event-resolver.component";

@NgModule({
  declarations: [
    AppComponent,
    EventListComponent,
    EventCardComponent,
    NavbarComponent,
    EventDetailsComponent,
    CreateEventComponent,
    Error404Component
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRoutes),
    FormsModule,
    HttpModule,
    ReactiveFormsModule,
    HttpModule,
    AlertModule.forRoot(),
    AngularFireModule.initializeApp(firebaseConfig),
    MaterialModule
  ],
  providers: [
    EventService,
    EventRouteActivator,
    EventListResolver,
    EventResolver,
    AppConfig
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



