import { Routes } from "@angular/router";
import { EventListComponent } from "./event-list/event-list.component";
import { EventDetailsComponent } from "./event-details/event-details.component";
import { CreateEventComponent } from "app/create-event/create-event.component";
import { Error404Component } from "app/error-404/error-404.component";
import { EventListResolver } from "app/event-list/event-list-resolver.component";
import { EventResolver } from "app/event-details/event-resolver.component";

export const appRoutes: Routes = [
    { path: 'events/new', component: CreateEventComponent },
    { path: 'events', component: EventListComponent, resolve:{events: EventListResolver} },
    { path: '404', component: Error404Component },
    { path: 'events/:id', component: EventDetailsComponent, resolve:{event:EventResolver} },
    { path: '', redirectTo: '/events', pathMatch: 'full' }
];  


